.highlight {
  position: relative;
  display: inline-block;
  z-index: 2000; }
  .highlight__line {
    position: absolute;
    top: 50%;
    height: 50%;
    width: 100%;
    background: #eeeeee;
    z-index: -100; }
    .highlight__line.strong {
      background: #dbcfff; }



